.add-beneficiary-section {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
        font-size: 30px;
    }

    p {
        font-weight: bold;
    }

    form {
        display: block;
        width: 100%;

        input[type=text] {
            display: inline-block;
            color: #fff;
            width: 45%;
            height: 60px;
            font-size: 20px;
            box-sizing: border-box;
            background-color: hsla(0, 0%, 100%, .25);
            border: none;
            border-radius: 1rem;
            padding: 1rem;
        }

        button {
            margin-left: 1rem;
            width: 10%;
        }
    }

    .beneficiary-list {
        width: 75%;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(500px, 5fr));
        grid-gap: 1rem;
        justify-items: center;
        margin: 0;
        padding: 0;

        .beneficiary-list-item {
            color: rgb(0, 200, 255);
        }
    }

    form {
        display: block;
        margin: 1rem;

        input[type=number] {
            display: inline-block;
            display: flexbox;
            color: rgb(0, 0, 0);
            width: 25%;
            height: 50px;
            font-size: 18px;
            padding: 1rem;
            box-sizing: border-box;
            background-color: white;
            border: none;
            border-radius: 1rem;
        }

        input[type='number']::-webkit-inner-spin-button,
        input[type='number']::-webkit-outer-spin-button {
            -webkit-appearance: none;
            appearance: none;
            margin: 0;
        }

        input[type=number],
        input[type=number]:hover,
        input[type=number]:focus {
            -moz-appearance: textfield;
        }
    }
}

.start-vesting {
    width: 35%;
}