.vesting-section {
    color: white;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.vesting-box {
    background-color: white;
    color: black;
    padding: 1rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
}

.details-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    div {
        padding: 0.5rem;
    }

    .details-text {
        font-weight: bold;
    }
}

button {
    cursor: pointer;
    color: #fff;
    border: none;
    font-weight: bold;
    font-family: inherit;
    padding: 1.2rem 2rem;
    text-transform: uppercase;
    border-radius: 3rem;
    font-size: 1.2rem;
    background-image: linear-gradient(to right,
            #00ff62 0%,
            #00ff88 0%,
            #00ff88 21%,
            #00fff2 52%,
            #00eeff 78%,
            #00e1ff 100%);
    background-size: 200% 200%;
    animation: gradient-animation 4s ease infinite;
}

button:hover {
    background-color: #121212;
}

/* KeyFrames */
@-webkit-keyframes gradient-animation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

@-moz-keyframes gradient-animation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradient-animation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}